.timeline-tree-wrapper {
  margin: 2.5rem 0;
}

.card {
  -webkit-box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
          box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  background-color: white;
}

.card__header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  padding: 1rem 1rem 1rem 2.5rem;
  border-bottom: solid 1px var(--light-gray);
}

.card__title {
  -ms-flex: 1 1;
      flex: 1 1;
  margin: auto 0;
  color: var(--dark-gray);
  font-size: 1.6rem;
}

.card__year-control {
  -ms-flex-line-pack: end;
      align-content: flex-end;
}

.year-control-title {
  font-size: 1.2rem;
  color: var(--thl-gray);
  text-align: center;
  margin: 0 0 0.5rem 0;
}

.card--collapsed {
  max-height: 0;
  margin: 0;
  overflow-y: hidden;
}



